import { mapActions, mapGetters } from "vuex";

import MainHeader from "@/components/MainHeader/MainHeader.vue";
import SearchBlock from "@/components/SearchBlock/SearchBlock.vue";
import PreloaderBlock from "@/components/UI/PreloaderBlock/PreloaderBlock.vue";
import PopularNews from "@/components/PopularNews/PopularNews.vue";
import NewsBlock from "@/components/NewsBlock/NewsBlock.vue";

export default {
  components: {
    MainHeader,
    SearchBlock,
    PreloaderBlock,
    PopularNews,
    NewsBlock,
  },

  methods: {
    ...mapActions({
      setHeaderLight: "header/setHeaderLight",
      fetchPopularNews: "news/fetchPopularNews",
      getDate: "date/getDate",
    }),
  },

  computed: {
    ...mapGetters({
      isNewsLoading: "news/isNewsLoading",
    }),
  },

  created() {
    this.setHeaderLight();
    this.getDate();
    this.fetchPopularNews();
  },
};
